/*
 * WooCommerce Fluid Checkout - Layout: Multi Step Enhanced
 */
 
.wfc-wrapper {
  position: relative;
  width: 100%;
}


@media( min-width: 1000px ) {

  .wfc-checkout-steps--active .wfc-checkout-steps {
    float: left;
    width: 57%;
    margin: 0;
  }
  
  .wfc-checkout-steps--active .wfc-checkout-order-review-wrapper {
    float: right;
    width: 40%;
    margin: 0;
  }
  
}




/**
 * CONTACT DETAILS
 */
.logged-in .wfc-contact-user-data ~ .wfc-contact-fields__wrapper {
  display: none;
}
